.contact-page {
  padding-top: 198px;
  padding-left: 10px;
  padding-right: 10px;
  transition: opacity 700ms cubic-bezier(.694, 0, .335, 1);
  opacity: 0;

  @include max-screen(480px) {
    padding-top: 90px;
  }

  &.is-loaded {
    opacity: 1;

    .contact-page__holder {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }

  &__title {
    color: $white;
    margin-bottom: 36px;
    font-size: 38px;

    @include max-screen(480px) {
      font-size: 28px;
    }

    strong {
      display: inline-block;

      &:not(:last-of-type) {
        margin-bottom: 12px;
      }
    }
  }

  &__holder {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    list-style: none;
    flex-wrap: wrap;
    transform: translate3d(0, 100%, 0);
    opacity: 0;
    transition: opacity 200ms,
      transform 400ms;
  }

  &__list {
    border-style: solid;
    border-width: 1px;
    border-color: rgba(255, 255, 255, .4);
    margin: 6px;
    max-width: 360px;
    width: 100%;
    flex: 1 0 360px;
    // transform: translate3d(0, 100, 0);
    // opacity: 0;
    // transition: opacity 200ms,
    // transform 400ms, border-color 200ms;
    transition: border-color 200ms;

    @include max-screen(480px) {
      max-width: 100%;
    }

    &:hover {
      border-color: $white;
    }

  }

  &__link {
    text-decoration: none;
    color: $white;
    display: flex;
    flex-direction: column;
    padding: 16px;
  }

  &__icon {
    width: 26px;
    height: 26px;
    margin-bottom: 12px;
  }

  &__cta {
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 8px;
  }

  &__ctn {
    margin: 0;
    font-size: 18px;
  }
}