.descr-restaurant {
  padding-top: 198px;
  transition: opacity 700ms cubic-bezier(.694, 0, .335, 1);
  opacity: 0;

  @include max-screen(480px) {
    padding-top: 90px;
  }

  &.is-loaded {
    opacity: 1;

    .descr-restaurant__txt,
    .descr-restaurant__recommend {
      transform: translate3d(0, 0, 0);
      opacity: 1;

    }
  }

  .container {
    color: #fff;
  }

  &__title {
    font-size: 66px;
    color: #fff;
    margin-bottom: 34px;

    @include max-screen(480px) {
      font-size: 54px;
      margin-bottom: 26px;
    }
  }

  &__txt {
    font-size: 18px;
    line-height: 32px;
    font-weight: 300;
    max-width: 76%;
    width: 100%;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 26px;
    transform: translate3d(0, 100%, 0);
    opacity: 0;
    transition: opacity 400ms,
      transform 600ms;

    @include max-screen(480px) {
      max-width: 100%;
      font-size: 16px;
    }
  }

  &__recommend {
    text-align: center;
    transform: translate3d(0, 100%, 0);
    opacity: 0;
    transition: opacity 600ms,
      transform 800ms;

    strong {
      font-style: italic;
    }
  }
}