// Header styles
.header {
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 999;
  padding: 16px 0;
  background-color: $main-color;

  @include max-screen(1024px) {
    transition: 0.3s ease;
  }

  &.is-fixed {}
}

.header-holder {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;

  @include max-screen(1024px) {
    max-width: 94%
  }
}

// Logo styles
.logo {
  height: 66px;
  width: 66px;
  flex: 0 0 auto;

  @include max-screen(1024px) {
    height: 60px;
    width: 60px;
  }

  @include max-screen(480px) {
    height: 48px;
    width: 48px;
  }
}

// Navigation styles
.nav {
  margin-left: auto;
  margin-right: 16px;

  @include max-screen(1024px) {
    position: fixed;
    top: 92px;
    right: 0;
    height: calc(100vh - 92px);
    width: 100%;
    transform: translate(100%, 0);
    background-color: rgb(0 0 0 / 80%);
    margin: 0;
    opacity: 0;
    z-index: -1;
    transition: 0.1s linear;
  }

  @include max-screen(480px) {
    top: 80px;
    height: calc(100vh - 80px);
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    @include max-screen(1024px) {
      justify-content: flex-start;
      flex-direction: column;
      align-items: flex-end;
      height: 100%;
      width: 50%;
      margin-left: auto;
      border-left: 1px solid rgb(255 255 255 / 60%);
      background-color: #000;
      transition: 0.2s ease-in-out;
      overflow-y: scroll;
      padding-top: 44px;
    }

    @include max-screen(560px) {
      width: 46%;
    }

    @include max-screen(480px) {
      width: 100%;
      border-left: none;
    }
  }

  li {
    margin: 0 8px;

    @include max-screen(1024px) {
      width: 100%;
    }

    @include max-screen(768px) {
      margin: 0 0 22px 0;
    }

    a {
      font-family: Gilroy-Semibold, sans-serif;
      text-decoration: none;
      font-size: 24px;
      display: flex;
      color: $white;
      padding: 4px 6px;
      opacity: 1;
      position: relative;
      transition: 0.1s linear;

      @include max-screen(1024px) {
        display: block;
        text-align: right;
      }

      @include max-screen(992px) {
        font-size: 32px;
      }

      @include max-screen(768px) {
        line-height: 1.25;
        padding: 0 10px;
      }

      &:after {
        content: '';
        display: block;
        width: 0;
        height: 1px;
        background-color: $white;
        position: absolute;
        left: 6px;
        bottom: 0;
        transition: 0.2s ease;
      }

      &:hover {
        opacity: 0.7;

        @media only screen and (min-width: 1025px) {
          &:after {
            width: calc(100% - 12px);
            opacity: 1;
          }
        }
      }

      &[data-btn="reserv"],
      &[data-btn="reserv"] {
        &:hover {
          opacity: 1;
        }
      }
    }

  }

  &.is-open {
    @include max-screen(1024px) {
      opacity: 1;
      z-index: 999;
      transform: translate(0%, 0);
    }
  }
}

//Holder-block styles
.holder-block {
  display: flex;
  align-items: center;
}

// Soc-link styles
.soc-link {
  display: flex;
  height: 26px;
  width: 26px;
  background-image: url(../../img/inst-logo.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  transition: 0.2s linear;
  transform: scale(1);
  transform-origin: center;

  &:hover {
    @media only screen and (min-width: 1025px) {
      transform: scale(0.9);
    }
  }

  &:active {
    transform: scale(0.8);
  }
}

// Burger mobile styles
.burger {
  height: 38px;
  width: 38px;
  background-color: $transp;
  border: none;
  padding: 0;
  position: relative;
  margin-left: 18px;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;

  @media only screen and (max-width: 1024px) {
    display: flex;
    margin-left: 38px;
  }

  span {
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $white;
    transition: 0.3s ease;

    &:first-child {
      top: 12px;
    }

    &:last-child {
      bottom: 12px;
    }
  }

  &.is-active {

    @include max-screen(1024px) {
      span {
        &:last-child {
          width: 70%
        }
      }
    }
  }
}

#resmio-kyko-restaurant {
  position: absolute;
  transform: translate(0, 40px);
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  z-index: -1;

  @include max-screen(1024px) {
    left: auto;
    right: 0;
    transform: translate(0, 50px);
    width: 100%;
  }

  &.is-visible {
    opacity: 1;
    visibility: visible;
    z-index: 800;
  }

  iframe {
    // opacity: 0;

  }
}