.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  z-index: 1000;
  background-color: $main-color;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s ease-in-out;

  &__holder {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    padding: 0 5px;

    &:after {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: $main-color;
      transition: 0.5s linear;
    }
  }

  &__main {
    font-size: 130px;
    line-height: 1.25;
    color: $white;
    margin-right: 24px;
    opacity: 0;
    transition: opacity 1.5s linear;

    @include max-screen(1024px) {
      font-size: 110px;
    }

    @include max-screen(768px) {
      font-size: 90px;
    }

    @include max-screen(580px) {
      font-size: 70px;
      margin-right: 16px;
    }

    @include max-screen(460px) {
      font-size: 48px;
      margin-right: 12px;
    }
  }

  &__submain {
    font-size: 50px;
    line-height: 1;
    font-weight: 300;
    color: $accent;

    @include max-screen(1024px) {
      font-size: 30px;
    }

    @include max-screen(580px) {
      font-size: 24px;
    }

    @include max-screen(460px) {
      font-size: 16px;
    }

    p {
      margin-bottom: 6px;

      &:last-child {
        margin: 0;
      }
    }
  }

  &.is-animation {
    animation-name: animate-preload;
    animation-duration: 0.8s;
    animation-delay: 1.7s;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;

    .preloader__holder {
      &:after {
        width: 0;
      }
    }

    .preloader__main {
      opacity: 1;
    }
  }
}

@keyframes animate-preload {
  from {
    transform: translate(0, 0);
    opacity: 1;
  }

  to {
    transform: translate(100%, 0);
    opacity: 0;
  }
}