// Hero styles
.hero {
  height: 100%;
  padding-top: 98px;
  display: flex;
  align-items: center;
  justify-content: center;

  @include max-screen(1024px) {
    max-width: 94%;
    margin: 0 auto;
  }

  &__txt {
    text-align: center;
    font-size: 166px;
    color: #fff;
    transition: .3s cubic-bezier(0.6, 1, 1, 1);
    transform: translate(0, 0);

    @include max-screen(1024px) {
      font-size: 146px;
    }

    @include max-screen(992px) {
      font-size: 126px;
    }

    @include max-screen(768px) {
      font-size: 106px;
    }

    @include max-screen(680px) {
      font-size: 86px;
    }

    @include max-screen(560px) {
      font-size: 66px;
    }

    &.is-shift {

      @include max-screen(1024px) {
        transform: translate(-58%, 0);
      }

      @include max-screen(560px) {
        transform: translate(-72%, 0);
      }
    }
  }
}

























// Develop
.develop {
  background-color: #000;
  color: #fff;
  padding: 6px;
  position: relative;
  text-align: center;
  font-size: 14px;

  a {
    color: #fff;
    text-decoration: none;
    text-align: center;
    position: relative;
    display: inline-block;

    &:hover {
      &:after {
        transform: scale(1);
      }
    }

    &:after {
      content: '';
      display: block;
      left: 0;
      right: 0;
      bottom: 0;
      height: 1px;
      width: 100%;
      background-color: #fff;
      transition: 0.3s ease;
      transform: scale(0);
    }
  }
}